@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
img {
  max-width: 100%;
  display: block;
}

a {
  text-decoration: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page__heading {
  margin: 0;
  padding: 0;
  font-weight: 700;
  font-size: 27px;
  line-height: 30px;
  color: #000000;
}
@media (min-width: 768px) {
  .page__heading {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .page__heading {
    font-size: 53px;
    line-height: 57px;
  }
}

.heading {
  padding: 0;
  margin: 0;
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
}
@media (min-width: 768px) {
  .heading {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .heading {
    font-size: 53px;
    line-height: 57px;
  }
}

.button {
  color: #444444;
  background-color: #ffffff;
  border: 2px solid #444444;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  text-transform: uppercase;
  padding: 14px 0;
  cursor: pointer;
}

.button--primary {
  width: 100%;
}
@media (min-width: 768px) {
  .button--primary {
    width: inherit;
    font-size: 17px;
  }
  .button--primary:hover {
    background-color: #444444;
    color: #ffffff;
  }
}
.button--primary:active {
  color: rgba(255, 255, 255, 0.3);
}

.button--form {
  font-size: 17px;
  line-height: 16px;
  padding: 16px 0;
}

.button--make-order {
  width: 100%;
}
@media (min-width: 768px) {
  .button--make-order {
    width: inherit;
    font-size: 17px;
  }
}
.button--make-order:hover {
  background-color: #444444;
  color: #ffffff;
}
.button--make-order:active {
  color: rgba(255, 255, 255, 0.3);
}

.button--slider {
  border: 1px solid #ececec;
  padding: 12px 38px;
}
@media (min-width: 768px) {
  .button--slider {
    width: inherit;
    border: none;
    padding: 10px;
  }
}
.button--slider svg {
  fill: #000000;
}
.button--slider:hover svg {
  opacity: 0.6;
}
.button--slider:active svg {
  opacity: 0.3;
}

@media (max-width: 767px) {
  .edging {
    background: url("../img/edging(m).svg");
    min-width: 145vw;
    max-width: 180vw;
    height: 6px;
  }
}
@media (min-width: 768px) {
  .edging {
    background: url("../img/edging(t).svg");
    height: 17px;
    min-width: 145vw;
    max-width: 180vw;
  }
}
@media (min-width: 1150px) {
  .edging {
    background: url("../img/edging(d).svg");
    height: 23px;
    min-width: 100%;
  }
}

.page {
  min-width: 320px;
  max-width: 767px;
  height: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .page {
    max-width: 768px;
  }
}
@media (min-width: 1150px) {
  .page {
    max-width: 1150px;
  }
}

.page__body {
  margin: 0;
  height: 100%;
  overflow-x: hidden;
}

.page__content {
  min-height: 100%;
}
@media (min-width: 768px) {
  .page__content {
    padding: 0 50px;
  }
}

.inner-header {
  width: 100%;
  background-color: #f1f1f1;
  text-align: center;
  padding: 73px 35px;
}
@media (min-width: 768px) {
  .inner-header {
    padding-top: 103px;
    padding-bottom: 65px;
  }
}
@media (min-width: 1150px) {
  .inner-header {
    padding-top: 100px;
    padding-bottom: 82px;
  }
}

.main-header {
  background-color: transparent;
}

.main-navigation {
  line-height: 24px;
  font-weight: 700;
  color: #000000;
  display: flex;
  flex-direction: column;
  position: relative;
}
@media (min-width: 768px) {
  .main-navigation {
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.main-header__logo {
  height: 80px;
  align-self: center;
  padding: 22px 40px;
}
@media (min-width: 768px) {
  .main-header__logo {
    width: 370px;
    padding: 18px 47px;
  }
}
@media (min-width: 1150px) {
  .main-header__logo {
    order: 2;
    padding: 5px 40px;
    width: 230px;
  }
}
.main-header__logo:hover {
  opacity: 60%;
}
.main-header__logo:active {
  opacity: 30%;
}

.main-navigation__toggle {
  position: absolute;
  background: url("../img/icons/menu-open.svg") no-repeat center;
  width: 40px;
  height: 40px;
  border: none;
  cursor: pointer;
  right: 7%;
  top: 24%;
}
@media (min-width: 768px) {
  .main-navigation__toggle {
    display: none;
  }
}
@media (min-width: 1150px) {
  .main-navigation__toggle {
    display: none;
  }
}

.main-navigation__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.main-navigation__item {
  padding: 28px 0;
}
@media (max-width: 767px) {
  .main-navigation__item {
    padding: 0;
    grid-column: 2/3;
    border-bottom: 1px solid #ececec;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
@media (min-width: 768px) {
  .main-navigation__item {
    padding: 22px 0;
  }
}
@media (min-width: 1150px) {
  .main-navigation__item {
    padding: 33px 20px;
  }
}

.main-navigation__link {
  color: #000000;
  font-size: 17px;
  width: 100%;
  padding: 28px 48px;
}
@media (max-width: 767px) {
  .main-navigation__link {
    padding: 28px 20%;
    display: grid;
    grid-template-columns: 30px max-content;
  }
  .main-navigation__link span {
    grid-column: 2/3;
    text-align: left;
  }
}
.main-navigation__link svg {
  opacity: 0.2;
}
.main-navigation__link:hover span {
  opacity: 0.6;
}
.main-navigation__link:hover svg {
  fill: #63d1bb;
  opacity: 1;
}
.main-navigation__link:active span {
  opacity: 0.3;
}
.main-navigation__link:active svg {
  fill: #63d1bb;
  opacity: 0.3;
}

.main-navigation__delivery {
  margin: 0;
  font-size: 15px;
  line-height: 24px;
  font-weight: 400;
  color: #444444;
  display: none;
}
@media (min-width: 768px) {
  .main-navigation__delivery {
    display: block;
    align-self: center;
    text-align: right;
    width: 359px;
    padding-right: 20px;
    background-color: transparent;
    z-index: 1;
  }
}
@media (min-width: 1150px) {
  .main-navigation__delivery {
    display: block;
    padding: 17px 51px;
  }
}

@media (min-width: 768px) {
  .main-navigation__wrapper {
    display: flex;
    flex-wrap: wrap;
    order: 4;
    justify-content: flex-end;
    background-color: transparent;
    z-index: 1;
  }
}
@media (min-width: 1150px) {
  .main-navigation__wrapper {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
}

.main-navigation__divider {
  position: relative;
}
@media (min-width: 768px) {
  .main-navigation__divider {
    height: 1px;
    width: 100%;
    order: 1;
  }
}
@media (min-width: 1150px) {
  .main-navigation__divider {
    order: 3;
  }
}

@media (min-width: 768px) {
  .main-navigation__divider::after {
    content: "";
    height: 1px;
    background-color: rgba(0, 0, 0, 0.1);
    width: 400vw;
    position: absolute;
    z-index: 2;
    left: -300%;
  }
}

.site-list--closed,
.site-list {
  position: absolute;
  top: 102%;
  background-color: #ffffff;
  z-index: 1;
  width: 100%;
}
@media (max-width: 767px) {
  .site-list--closed,
.site-list {
    display: grid;
    grid-template-columns: 1fr 100% 1fr;
  }
}
@media (min-width: 768px) {
  .site-list--closed,
.site-list {
    display: flex;
    flex-wrap: wrap;
    order: 2;
    width: 380px;
    padding-left: 30px;
    position: static;
    background-color: transparent;
  }
}
@media (min-width: 1150px) {
  .site-list--closed,
.site-list {
    display: flex;
    flex-wrap: wrap;
    order: 1;
    background-color: #ffffff;
    width: 460px;
  }
}

@media (max-width: 767px) {
  .site-list--closed {
    display: none;
  }
}

@media (min-width: 768px) {
  .site-list__link {
    font-size: 15px;
    padding: 20px;
  }
}
@media (min-width: 1150px) {
  .site-list__link {
    font-size: 17px;
    padding: 15px;
  }
}

@media (min-width: 1150px) {
  .site-list__item--catalog {
    border-right: 1px solid rgba(0, 0, 0, 0.1);
    padding-left: 5px;
    padding-right: 20px;
  }
}

.user-list--closed,
.user-list {
  position: absolute;
  order: 1;
  top: 305%;
  background-color: #ffffff;
  z-index: 2;
  width: 100%;
}
@media (max-width: 767px) {
  .user-list--closed,
.user-list {
    display: grid;
    grid-template-columns: 1fr 100% 1fr;
  }
}
@media (min-width: 768px) {
  .user-list--closed,
.user-list {
    display: flex;
    flex-wrap: wrap;
    width: 398px;
    position: static;
  }
}
@media (min-width: 1150px) {
  .user-list--closed,
.user-list {
    display: flex;
    flex-wrap: wrap;
    order: 3;
    width: 450px;
    padding-left: 60px;
  }
}

@media (max-width: 767px) {
  .user-list--closed {
    display: none;
  }
}

@media (min-width: 768px) {
  .main-navigation__link--search-link {
    display: flex;
    padding: 35px 32px;
    padding-bottom: 28px;
  }
  .main-navigation__link--search-link span {
    color: transparent;
    font-size: 0;
  }
}

@media (min-width: 768px) {
  .main-navigation__item--search-item {
    border-right: 1px solid rgba(0, 0, 0, 0.1);
    border-left: 1px solid rgba(0, 0, 0, 0.1);
    padding: 0;
  }
}

@media (min-width: 768px) {
  .main-navigation__link--cart-link {
    display: flex;
    padding: 32px;
    padding-bottom: 28px;
    align-items: center;
    gap: 32px;
  }
}

@media (min-width: 768px) {
  .main-navigation__item--cart-item {
    padding: 0;
  }
}

.promo-menu {
  display: none;
}
@media (min-width: 768px) {
  .promo-menu {
    display: none;
  }
}
@media (min-width: 1150px) {
  .promo-menu {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    width: 480px;
  }
}

.promo-menu__item {
  padding: 16px 27px;
  padding-right: 13px;
}

.promo-menu__item:nth-child(2n) {
  padding: 16px 0;
}

.promo-menu__link {
  color: #000000;
  font-weight: 700;
  font-size: 15px;
  line-height: 24px;
  padding: 12px 24px;
}
.promo-menu__link:hover span {
  opacity: 0.6;
}
.promo-menu__link:active span {
  opacity: 0.3;
}

.features {
  height: 488px;
  background: url("../img/features-underlay(m).png") no-repeat center;
  background-position-y: -20px;
  margin: 0 auto;
  margin-bottom: 27px;
  background-color: #f9f9f9;
}
@media (max-width: 767px) {
  .features {
    max-width: 767px;
    position: relative;
  }
}
@media (max-width: 767px) and (min-resolution: 2dppx) {
  .features {
    background-image: url("../img/features-underlay(m)@2x.png");
    background-size: 320px auto;
  }
}
@media (min-width: 768px) {
  .features {
    height: 760px;
    background: url("../img/features-underlay(t).png") center no-repeat;
    margin-top: -72px;
    margin-bottom: 43px;
    position: relative;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .features {
    background-image: url("../img/features-underlay(t)@2x.png");
    background-size: 768px auto;
  }
}
@media (min-width: 1150px) {
  .features {
    background: url("../img/features-underlay(d).png") center no-repeat;
    margin-top: -62px;
    margin-bottom: 70px;
    background-position-y: 1px;
  }
}
@media (min-width: 1150px) and (min-resolution: 2dppx) {
  .features {
    background-image: url("../img/features-underlay(d)@2x.png");
    background-size: 1150px auto;
  }
}

@media (max-width: 767px) {
  .features::before,
.features::after {
    content: "";
    position: absolute;
    height: 280px;
    width: 70vw;
    background-color: #f9f9f9;
    top: 0;
    z-index: -1;
  }
}
@media (min-width: 768px) {
  .features::before,
.features::after {
    content: "";
    position: absolute;
    height: 99%;
    width: 95vw;
    background-color: #f9f9f9;
    top: 4px;
    z-index: -1;
  }
}

.features::before {
  right: 0;
}

.features::after {
  left: 0;
}

.features__title {
  text-align: center;
  margin: 0 auto;
  padding-top: 70px;
  margin-bottom: 120px;
}
@media (min-width: 768px) {
  .features__title {
    padding-top: 160px;
    margin-bottom: 371px;
  }
  .features__title br {
    display: none;
  }
}
@media (min-width: 1150px) {
  .features__title {
    width: 800px;
    padding-top: 159px;
    margin-bottom: 355px;
  }
}

.features__list-wrapper {
  position: relative;
}

@media (min-width: 768px) {
  .features__edging {
    background: url("../img/features-edging(t).svg");
    min-width: 145vw;
    max-width: 180vw;
    height: 24px;
    position: absolute;
    bottom: -3px;
    left: -50vw;
    right: 0;
  }
}
@media (min-width: 1150px) {
  .features__edging {
    background: url("../img/features-edging(d).svg");
    height: 34px;
    position: absolute;
    bottom: -15px;
    min-width: 150vw;
  }
}

.features__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .features__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.features__item:nth-child(odd) {
  background-color: #63d1bb;
  padding-right: 36px;
}
@media (max-width: 767px) {
  .features__item:nth-child(odd) a {
    padding-top: 26px;
    padding-bottom: 15px;
  }
}

.features__item:nth-child(even) {
  background-color: #56c3b2;
  padding-right: 27px;
}
@media (min-width: 768px) {
  .features__item:nth-child(even) {
    padding-right: 34px;
  }
}
@media (max-width: 767px) {
  .features__item:nth-child(even) a {
    padding-top: 25px;
    padding-bottom: 20px;
  }
}

.features__link {
  display: flex;
  padding-left: 30px;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .features__link {
    padding-top: 25px;
    padding-bottom: 35px;
    padding-left: 35px;
  }
}
@media (min-width: 1150px) {
  .features__link {
    padding-bottom: 28px;
  }
}
.features__link svg {
  fill: #149080;
  opacity: 0.5;
}
@media (min-width: 768px) {
  .features__link svg {
    margin-top: 6px;
  }
}

@media (min-width: 768px) {
  .features__item:hover {
    margin-top: -5px;
    transform: translateY(-5px);
  }
}

.features__link:hover svg {
  opacity: 0.7;
}

.features__link:active svg {
  opacity: 0.3;
}

.features__link-text {
  color: #ffffff;
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
}
@media (min-width: 768px) {
  .features__link-text {
    font-size: 20px;
    line-height: 30px;
  }
}

.offer {
  position: relative;
}
@media (max-width: 767px) {
  .offer {
    width: 320px;
    margin: 0 auto;
    padding: 0 30px;
  }
}

.offer__title {
  padding: 4px 0;
}
@media (min-width: 768px) {
  .offer__title {
    padding: 0;
  }
}

.offer__subtitle {
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  color: #63d1bb;
  text-transform: uppercase;
  padding: 4px 0;
}
.offer__subtitle br {
  display: none;
}
@media (min-width: 768px) {
  .offer__subtitle {
    line-height: 17px;
    padding: 0;
    display: flex;
    align-self: center;
    text-align: center;
    padding-bottom: 7px;
  }
  .offer__subtitle br {
    display: block;
  }
}

@media (min-width: 768px) {
  .offer__title-wrapper {
    display: flex;
    justify-content: space-between;
    padding-right: 27px;
  }
}
@media (min-width: 1150px) {
  .offer__title-wrapper {
    flex-direction: row-reverse;
    justify-content: flex-end;
    gap: 58px;
