    padding-left: 45px;
    padding-top: 49px;
  }
}

@media (min-width: 768px) {
  .offer__content-wrapper {
    display: grid;
    grid-template-columns: 292px 334px;
    grid-template-rows: 94px 121px 110px;
    column-gap: 42px;
