@media (min-width: 1150px) {
  .offer__content-wrapper picture {
    grid-row: 1/5;
  }
}

.offer__description {
  font-size: 15px;
  line-height: 24px;
  color: #444444;
}
@media (min-width: 768px) {
  .offer__description {
    font-size: 17px;
    line-height: 30px;
    padding: 0;
    margin: 0;
    grid-column: 1/2;
  }
}
@media (min-width: 1150px) {
  .offer__description {
    grid-row: 2/3;
    padding-left: 65px;
  }
}

.offer__table {
  font-size: 15px;
  line-height: 20px;
  color: #444444;
  padding: 9px 0;
  width: 100%;
}
@media (min-width: 768px) {
  .offer__table {
    padding: 0;
    max-height: 100px;
    grid-column: 1/2;
  }
}
@media (min-width: 1150px) {
  .offer__table {
    grid-row: 3/4;
    padding-left: 65px;
  }
}

@media (min-width: 768px) {
  .offer__table-row {
    display: grid;
    grid-template-columns: 97px 1fr;
    margin-left: -3px;
    width: 292px;
  }
}

.offer__table-digit {
  padding-bottom: 15px;
  border-bottom: 1px solid #ececec;
}
@media (min-width: 768px) {
  .offer__table-digit {
    padding-bottom: 12px;
    margin-bottom: 6px;
  }
}

.offer__image {
  width: 100%;
  height: 100%;
}
@media (max-width: 767px) {
  .offer__image {
    padding: 15px 0;
    padding-bottom: 27px;
  }
}
@media (min-width: 768px) {
  .offer__image {
    margin-top: 11px;
  }
}
@media (min-width: 1150px) {
  .offer__image {
    margin-top: -49px;
    height: 636px;
  }
}

.offer__price {
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #444444;
