    padding-top: 19px;
  }
}
@media (min-width: 1150px) {
  .legend-additional {
    margin-left: 57px;
  }
}

@media (min-width: 768px) {
  .fieldset-additional-label {
    grid-column: 2/3;
  }
}

.legend-additional::after {
  content: "";
  height: 1px;
  width: 463%;
  position: absolute;
  top: 50%;
  left: 145%;
  background-color: #63d1bb;
  z-index: 1;
}
@media (min-width: 768px) {
  .legend-additional::after {
    display: none;
  }
}

.form-input--textarea {
  resize: none;
  width: 100%;
  height: 142px;
  background-color: #ffffff;
  border: 2px solid #444444;
  box-shadow: none;
  padding: 17px;
}
.form-input--textarea:focus-visible, .form-input--textarea:hover {
  border: 2px solid #62d1ba;
  outline: none;
  box-shadow: none;
}

.modal {
  background-color: rgba(255, 255, 255, 0.9);
  position: fixed;
  inset: 0 0 0 0;
  z-index: 10;
}

.modal__box {
  position: relative;
  top: 20%;
  display: flex;
  width: 100%;
  margin: 0 auto;
  padding: 28px 30px 46px;
  flex-direction: column;
  border: 1px solid #c6c6c6;
  background-color: #ffffff;
  box-sizing: border-box;
  box-shadow: 0 5px 10px rgba(0, 1, 1, 0.25);
}
@media (min-width: 768px) {
  .modal__box {
    width: 566px;
    padding: 38px 108px 46px;
  }
}

.modal__title {
  margin-bottom: 14px;
  color: #000000;
  font-size: 25px;
  line-height: 30px;
  text-transform: uppercase;
  text-align: center;
}

.modal__subtitle {
  margin-bottom: 21px;
  line-height: 30px;
  text-align: center;
}

.modal__close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 30px;
  height: 30px;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.modal__close-button::before {
  position: absolute;
  top: 50%;
  left: 0;
  width: 30px;
  height: 2px;
  content: "";
  background-color: #000000;
  transform: rotate(45deg);
}
.modal__close-button::after {
  position: absolute;
  top: 50%;
  left: 0;
  width: 30px;
  height: 2px;
  content: "";
  background-color: #000000;
  transform: rotate(-45deg);
}

.cart__field {
  display: flex;
  column-gap: 7px;
  margin: 0 0 31px;
  padding: 0;
  border: none;
}

.cart__radio-label {
  flex-grow: 1;
  cursor: pointer;
}
.cart__radio-label:hover .cart__radio {
  border-color: rgba(0, 0, 0, 0.3);
}

.cart__radio {
  display: block;
  padding: 16px 0;
  font-size: 17px;
  line-height: 16px;
  font-weight: 700;
  text-align: center;
  border: 2px solid rgba(0, 0, 0, 0.1);
}

.cart__radio-input:checked + .cart__radio {
  border-color: #62d1ba;
}

.cart__button {
  background-color: #63d1bb;
  color: #ffffff;
  border: none;
  width: 100%;
}